<template>
    <div>
        <b-jumbotron >

            <template v-slot:lead>
                <p v-html="currentQuestion.question"></p>
            </template>

            <hr class="my-4">
            <b-list-group
                    :class="{'disabled' :  answered }">
                <b-list-group-item v-for="(answer, index) in answers"
                                   :key="index"
                                   @click="selectAnswer(index)"
                                   :class="answerClass(index)"
                                   v-html="answer"
                >
                </b-list-group-item>
            </b-list-group>
            <b-button
                    variant="primary"
                    @click="submitAnswer"
                    :disabled="selectedIndex === null || answered"
            >
                Submit
            </b-button>
            <b-button
                    @click="next"
                    variant="success"
                    :disabled="index === questionsQuantity - 1 ||
                                selectedIndex === null || !answered "
            >Next</b-button>
        </b-jumbotron>
        <b-alert show variant="info"
                        v-if="index === questionsQuantity - 1 && answered">
           You Done it bitch!
            <br>
            Your score is {{ nmbCorrect }}/{{ nmbTotal }}
        </b-alert>
    </div>
</template>
<script>
    import _ from 'lodash'

    export default {
        props: {
            currentQuestion: Object,
            next: Function,
            increment: Function,
            questionsQuantity: Number,
            nmbCorrect: Number,
            nmbTotal: Number,
            index: Number
        },
        data(){
            return {
                selectedIndex: null,
                correctIndex: null,
                shuffledAnswers: [],
                answered: false
            }
        },
        computed: {
            answers(){
                let answers = [...this.currentQuestion.incorrect_answers]
                answers.push(this.currentQuestion.correct_answer)
                return answers
            }
        },
        watch: {
            currentQuestion: {
                immediate: true,
                handler() {
                    this.selectedIndex = null
                    this.shuffleAnswers()
                    this.answered = false
                }
            }
        },
        methods: {
            selectAnswer(index){
               this.selectedIndex = index
            },
            shuffleAnswers(){
                let answers = [...this.currentQuestion.incorrect_answers, this.currentQuestion.correct_answer ]
                this.shuffledAnswers = _.shuffle(answers)
                this.correctIndex = this.shuffledAnswers.indexOf(this.currentQuestion.correct_answer)

            },
            submitAnswer(){
                let isCorrect =false
                if (this.selectedIndex === this.correctIndex){
                    isCorrect = true
                }
                this.answered = true
                this.increment(isCorrect)
            },
            answerClass(index){
                let answerClass =''

                if(!this.answered && this.selectedIndex === index){
                    answerClass = 'selected'
                } else if( this.answered && this.correctIndex === index ){
                    answerClass = 'correct'
                }  else if(  this.answered  && this.selectedIndex === index &&  this.correctIndex !== index ){
                    answerClass = 'incorrect'
                }

                return answerClass
            }
        },
        mounted(){
            console.log(this.currentQuestion)
        }
    }


</script>
<style scoped>
    .list-group{
        margin-bottom: 20px;
    }
    .list-group.disabled{
        pointer-events: none;
    }
    .list-group-item:hover{
        background: #e9ecef;
        cursor: pointer;
    }
    .btn{
        margin: 0 5px;
    }
    .selected{
        background: lightblue;
    }
    .correct{
        background: lightgreen;
    }
    .incorrect{
        background: red;
    }
    .btn.disabled, .btn:disabled {
        opacity: 0.3;
    }
</style>